
export class AppSettings {
  // public static get WebApiUrl(): string { return 'http://twapi.logicaplus.net/'; }
  // public static get ResourceUrl(): string { return 'http://twapi.logicaplus.net/Anonymous/GetResources'; }
  // public static get IamHereUrl(): string { return 'http://twapi.logicaplus.net/Anonymous/IamHere'; }
  public static get CookiDomain(): string {return 'logicaplus.net';}
  public static get WebApiUrl(): string { return 'http://timewebapi.timeatt.local/'; }
  public static get ResourceUrl(): string { return 'http://timewebapi.timeatt.local/Anonymous/GetResources'; }
  public static get IamHereUrl(): string { return 'http://timewebapi.timeatt.local/Anonymous/IamHere'; }
  public static get DataTableLanguage() {
    return this.CurrentLang === 'ar' ? this.DataTableARLanguage : this.DataTableENLanguage;
  }

  public static CurrentLang: string = 'ar';
  public static resourceBundle = null;
  private static DataTableARLanguage = {
    "decimal": "",
    "emptyTable": "  ",
    "info": "  _START_  _END_   _TOTAL_",
    "infoEmpty": "",
    "infoFiltered": "(   _MAX_ )",
    "infoPostFix": "",
    "thousands": ",", 
    "lengthMenu": "_MENU_",
    "loadingRecords": "....",
    "processing": " ....",
    "search": '<span class="input-group-addon"><i class="glyphicon glyphicon-search"></i></span>',
    "zeroRecords": "    ",
    "paginate": {
      "first": "",
      "last": "",
      "next": "",
      "previous": ""
    },
    "aria": {
      "sortAscending": ":  ",
      "sortDescending": ":  "
    },
    "searchPlaceholder": "..."
  };
  private static DataTableENLanguage = {
    "decimal": "",
    "emptyTable": "No data available in table",
    "info": "Showing _START_ to _END_ of _TOTAL_ entries",
    "infoEmpty": "",
    "infoFiltered": "(filtered from _MAX_ total entries)",
    "infoPostFix": "",
    "thousands": ",",
    "lengthMenu": "_MENU_",
    "loadingRecords": "Loading...",
    "processing": "Processing...",
    "search": '<span class="input-group-addon"><i class="glyphicon glyphicon-search"></i></span>',
    "zeroRecords": "No matching records found",
    "paginate": {
      "first": "First",
      "last": "Last",
      "next": "Next",
      "previous": "Previous"
    },
    "aria": {
      "sortAscending": ": sort ascending",
      "sortDescending": ": sort descending"
    },
    "searchPlaceholder": "Search..."
  };
 public static get getCurrentLanguage():string{
    let _culture='ar'
    let cookie = document.cookie;
    if (cookie && cookie.length > 0 && cookie.indexOf("_culture") >= 0) {
      let cookiearry = document.cookie.split(";");
      let userinfo = '';
      cookiearry.forEach(e => {
        if (e.indexOf("_culture") >= 0) {
          _culture = e.split('_culture=')[1];
        }
      });
    }
    return _culture;
  }
}



